.WaypointsList {
    list-style: none;
    padding: 10px 0;
    margin: 0;
}

.WaypointsList-Item {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  cursor: pointer;
}

.WaypointsList-Item div {
  -ms-flex: 1 1;
      flex: 1 1;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.WaypointsList-Item button {
  -webkit-appearance: none;
  border: none;
  background: none;
  cursor: pointer;
}



